import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ConnectButton from './ConnectButton';
import { Slide } from '@mui/material';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import CssBaseline from '@mui/material/CssBaseline';

interface Props {
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window?: () => Window;
    children: React.ReactElement;
  }

function HideOnScroll(props: Props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
      target: window ? window() : undefined,
    });
  
    return (
      <Slide appear={false} direction="down" in={!trigger}>
        {children}
      </Slide>
    );
  }

interface AppBarProps {
  style?: React.CSSProperties;
  nickname: string;
}

export default function TopAppBar(props: AppBarProps) {
  return (
    <Box className={""} sx={{  }}>
      <CssBaseline />
      <HideOnScroll {...props}>
      <AppBar position="fixed" sx={{background: 'none', border: 'none', boxShadow: 'none'}}>
        <Toolbar>

          {/* <ConnectButton nickname={this.state.nickname} /> */}
          <div style={{minWidth: "300px", marginLeft: 'auto', marginRight: "16px"}}>
            <ConnectButton nickname={props.nickname} />
          </div>
        </Toolbar>
      </AppBar>
      </HideOnScroll>
    </Box>
  );
}