import React from 'react';
import logo from './logo.svg';
import './App.css';

import { styled } from '@mui/material/styles';
import { Button, Grid, Typography } from '@mui/material';
import { StandardTextFieldProps, TextField } from '@mui/material';


import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';

import { AbiItem, TransactionReceipt } from 'ethereum-abi-types-generator';

import ConnectButton from "./components/ConnectButton";

import diamondteamlogo from "./assets/img/mydiamondteam-fire.png";
import goku from "./assets/img/v2/goku-with-font.png";
import diamondteamlogo_header from "./assets/img/header-blank.png";

import {ContractContext, CreditsAndDebitsResponse, PayoutOfResponse, UserInfoResponse, UserInfoTotalsResponse, UsersBoostsResponse, UsersRealDepositsResponse} from './assets/abi/farm-contract';
import {ContractContext as Token_ContractContext} from './assets/abi/piston-contract';
import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";
import copy from 'clipboard-copy';
import { formatBnbToUSD, formatEthFromWei, formatNumber, formatWeiToUSD } from './Formatters';
import {ReactComponent as TelegramIcon} from './assets/img/Telegram_logo.svg';
import {ReactComponent as BUSDIcon} from './assets/img/busd.svg';
import ShieldIcon from '@mui/icons-material/VerifiedUser';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import PictureAsPdfIcon from '@mui/icons-material/PictureAsPdf';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import AirdropPanel from './components/AirdropPanel';
import Countdown from "react-countdown";

import PaymentsIcon from '@mui/icons-material/Payments';
import LocalDrinkIcon from '@mui/icons-material/LocalDrink';
import OpacityIcon from '@mui/icons-material/Opacity';
import SavingsIcon from '@mui/icons-material/Savings';
import SpeedIcon from '@mui/icons-material/Speed';
import AirplanemodeActiveIcon from '@mui/icons-material/AirplanemodeActive';

//
//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/farm-contract.json";
import abi_token from "./assets/abi/piston-contract.json";
import TopAppBar from './components/AppBar';




const contractAddress: string = '0xa930f5A3C7A55783e9074aCecceaE38F4f49bB56'; // release on testnet
const tokenContractAddress: string = '0xa7fb4C2ACe237f0FbAeD33C92Cb1c34f66FDAF99'; // release on testnet


//const contractAddress: string = '0x3AEDafF8FB09A4109Be8c10CF0c017d3f1F7DcDc'; // release on mainnet same address as testnet!

window.chainIdExpected = 97;
//window.chainIdExpected = 56;


declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    token_contract: Token_ContractContext;
    web3: Web3;
    walletConnectProvider: WalletConnectProvider,
    ethereum: any;
    contractAddress: string;
    tokenContractAddress: string;
    contract_abi: AbiItem[]
    token_contract_abi: AbiItem[]
    airdropContract_abi: AbiItem[]
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
    chainId: number;
    chainIdExpected: number;
    bnbPrice: number;
  }
}

interface AppProps {
  style?: React.CSSProperties;
}

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const modalStyle = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "70%",
  height: "60%",
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  color: "lightgrey",
  overflow: "auto",
  opacity: 0.95,
};

window.dataRefreshInterval = 3000;

class App extends React.Component<AppProps> {
  
  state = {
    balance: "",
    sponsorAddress: "",   
    referralFromUrl: "",    
    referredBy: "?",
    myUpline: "", // same like referreedBy but wallet address only. without text ...
    yourAddress: "?",    
    nickname: "",
    approveText: "approve",

    unwithdrawn: "",
    withdrawn: "",
    deposits: "",
    realDeposits: "",
    allowance: "",
    ref_bonus: "",
    maxPayout: "",
    playerinfo: "",
    netDeposits: "",
    myLastAirdropSent: "",
    myAirdrops: "",
    myAirdropsReceived: "",
    referral_team: "",
    upline_team: "",
    stakeBoostPSTN: "",
    stakeBoostBUSD: ""
  }

 // private defaultSponsorAddress: string = "0xb43aa594c3a40b6b788184925cd00f20b6f72ccf";
  private defaultSponsorAddress: string = "0x4ff8C8741256d729A8cC7F1Cc02330F8103CCD28"; // testnet
 // private defaultSponsorAddress: string = "0xfDC01397731d3280bBB5e2A136381297962C87d2"; //mainnet

  private amountInput: React.RefObject<HTMLInputElement>;
  private stakeAmountInput: React.RefObject<HTMLInputElement>;
  private sponsorInput: React.RefObject<StandardTextFieldProps>;
  private approveInput: React.RefObject<StandardTextFieldProps>;

  private timeWindowBase: number = 86400; // 86400 = 1 day; 3600 = 1 hour

  baseState: any;
  _stateInterval: any;

  constructor(props: AppProps) {
    super(props);

    this.baseState = this.state;
    // properties

    this.amountInput = React.createRef();
    this.stakeAmountInput = React.createRef();
    this.sponsorInput = React.createRef();
    this.approveInput = React.createRef();

  }

  private _modalInterval: any = null;
  private CountDownDate: Date = new Date();

  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];

    window.tokenContractAddress = tokenContractAddress;
    window.token_contract_abi = abi_token as AbiItem[];



    this.checkState();

    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);

    //this.setModalOpen();
  }

  componentWillUnmount = () => { 
    
  }

  onClick_Approve = () => {

    if( this.approveInput.current != null ){
      if(new Number(this.approveInput.current.value as string) >= 0){
        window.token_contract.methods.approve(window.contractAddress, /*max*/window.web3.utils.toWei(this.approveInput.current.value as string)).send({from: window.walletAddress});
      }else{
        alert("please set an amount to approve");
      }    
    }
  }

  checkState = () => {  

    const urlParams = new URLSearchParams(window.location.search);
    let refAddress = urlParams.get('ref');
    //
    if(refAddress != null){

      window.refferalAddress = refAddress;
      if(this.state.myUpline == "" || this.state.myUpline == "0x0000000000000000000000000000000000000000"){
        this.setState({
          referralFromUrl: "Referral bonus from your investment will go to " + window.refferalAddress
        });
      }else{
        this.setState({
          referralFromUrl: ""
        });
      }
      //console.log("refAddress", window.refferalAddress);

      if(this.sponsorInput.current != null){
        //this.sponsorInput.current.value = refAddress // ref address in sponsor txt
        this.setState({sponsorAddress: window.refferalAddress});
      }

    }  

    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }



    this.setState({
      yourAddress: window.walletAddress,
    })

    
    window.token_contract.methods.balanceOf(window.walletAddress).call().then(result => {
      this.setState({balance: result});

      window.token_contract.methods.allowance(window.walletAddress, window.contractAddress).call().then(result => {
        let allowance = window.web3.utils.fromWei(result);
        if(new Number(allowance) > 0){
          this.setState({
            approveText: "approved ",
            allowance: result
          });
        }
      }).catch((err) => {
        console.log(err)
      });

    }).catch((err:any) => {
      console.log(err);
    });

    window.farm_contract.methods.userInfo(window.walletAddress).call().then((result: UserInfoResponse) => {

      let lastSent = "never";
      if( result.last_airdrop != "0"){
        lastSent = new Date(parseInt(result.last_airdrop) * 1000 ).toLocaleString()
      }

      this.setState({
        withdrawn: result.payouts,
        deposits: result.deposits,
        referredBy: result.upline == "0x0000000000000000000000000000000000000000"? "Will show after deposit": result.upline,
        myUpline: result.upline,
        ref_bonus: result.match_bonus,
        myLastAirdropSent: lastSent

      });

      window.farm_contract.methods.maxPayoutOf(result.deposits).call().then((result) => {
        this.setState({
          maxPayout: result
        })
      });

      window.farm_contract.methods.userInfoTotals(window.walletAddress).call().then((result: UserInfoTotalsResponse) => {
        this.setState({
          playerinfo: `${result.referrals}/${result.total_structure}`,
          myAirdrops: result.airdrops_total,
          myAirdropsReceived: result.airdrops_received
        })
      });

      window.farm_contract.methods.creditsAndDebits(window.walletAddress).call().then((result: CreditsAndDebitsResponse) => {
        this.setState({
          netDeposits: result._credits
        })
      });

      window.farm_contract.methods.usersBoosts(window.walletAddress).call().then((result: UsersBoostsResponse) => {
        this.setState({
          stakeBoostPSTN: result.stakedBoost_PSTN,
          stakeBoostBUSD: result.stakedBoost_BUSD
        })
      });

      window.farm_contract.methods.usersRealDeposits(window.walletAddress).call().then((result: UsersRealDepositsResponse) => {
        this.setState({
          realDeposits: result.deposits
        })
      });


    });

    window.farm_contract.methods.payoutOf(window.walletAddress).call().then((result: PayoutOfResponse) => {
      this.setState({
        unwithdrawn: result.payout
      });
    });
    
  }


  setModalOpen = () => {
    this.setState({modalOpen: true});
  }

  setModalClose = () => {
    clearInterval(this._modalInterval);
    this._modalInterval = null;
    this.setState({modalOpen: false});
  }

  copyRefToClipboard = () => {
    let referralPart = (this.state.nickname)?this.state.nickname:window.walletAddress;
    let refAddress = "https://mydiamondteam.online/v2/" + "?ref=" + referralPart;
    copy(refAddress);
  }


  onClick_Invest = () => {
    if(!this.amountInput.current || !this.sponsorInput.current){
      return;
    }
    
      let ref = window.refferalAddress;
      if (!window.web3.utils.isAddress(ref)){
        ref=this.sponsorInput.current.value as string;
        //console.log("this.state.myUpline", this.state.myUpline)
        if(this.state.myUpline == "" || this.state.myUpline == "0x0000000000000000000000000000000000000000"){
          if(ref.length == 0 ){
            if(window.confirm("You cannot join without a sponsor. would you like to use the project's default sponsor to join?") == true){

                this.setState({sponsorAddress: this.defaultSponsorAddress});

            }else{
              return;
            }
          }else{
            if(!window.web3.utils.isAddress(ref) || ref == window.walletAddress){
              alert("Sponsor Addres not valid");
              return;
            }
          }
        }else{
          ref = this.state.myUpline; // wenn bereits upline vorhanden ist, dann immer diese verwenden. wird dann bei setUpline im contract sowieso ignoriert
        }
      }
      if(ref.length == 0){
        //ref = this.state.projectWallet;
        ref = this.defaultSponsorAddress;
        //console.log("Invest with Project Wallet as Sponsor.");
      }    

      // invest with upline address
      if(!this.amountInput.current){
        return;
      }

      console.debug("invest with address ", ref);
      window.farm_contract.methods.deposit( ref, window.web3.utils.toWei(this.amountInput.current.value as string) )
        .send({ from:window.walletAddress}).then((response:any) => {
    
        }).catch((err:any) => {
          alert("transaction failed");
        }
      );
  }

  onClick_Stake = () => {
      if(!this.stakeAmountInput.current){
        return;
      }
    
      window.farm_contract.methods.stakeBoost( window.web3.utils.toWei(this.stakeAmountInput.current.value as string) )
        .send({ from:window.walletAddress}).then((response:any) => {
    
        }).catch((err:any) => {
          alert("transaction failed");
        }
      );
  }

  onClick_Unstake = () => {
      window.farm_contract.methods.unstakeBoost( )
        .send({ from:window.walletAddress}).then((response:any) => {
    
        }).catch((err:any) => {
          alert("transaction failed");
        }
      );
  }

  onClick_Eject = () => {
      window.farm_contract.methods.eject( )
        .send({ from:window.walletAddress}).then((response:any) => {
    
        }).catch((err:any) => {
          alert("transaction failed");
        }
      );
  }

  onClick_Reinvest = () => {
    window.farm_contract.methods.roll().send({from: window.walletAddress/*, gasPrice: "18000000000"*/}).then((result: TransactionReceipt) => {
      alert("Re-Invest Transaction Submitted.")
    }).catch((err:any) => {
      alert("transaction failed");
    });
  }

  onClick_Withdraw = () => {
    window.farm_contract.methods.claim().send({from: window.walletAddress/*, gasPrice: "18000000000"*/}).then((result: TransactionReceipt) => {
      alert("Withdraw Transaction Submitted.")
    }).catch((err:any) => {
      alert("transaction failed");
    });
  }


  //  Main Layout
  //
  render = () => {
    return (
      <Box sx={{ flexGrow: 1 }} style={{ width: "90%", margin: "auto", height: "100%", marginTop: "32px" }}>
  <TopAppBar nickname={this.state.nickname}></TopAppBar>

  <Grid id="mainApp" container spacing={2} style={{marginTop: "64px"}}>
    <Grid item xs={12} md={12}>
      <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
        PISTON RACE - TESTNET - It's Better Than It Looks 
      </div>
    </Grid>
    <Grid item xs={12} md={12}>
      <Paper className={""} style={{ padding: "16px", paddingTop: "8px", minHeight: "306px" }}>


        <Grid container>
          <Grid item xs={12} md={6}>
            
            <Grid item xs={12} md={6}>
              <Typography sx={{ fontWeight: "bold", fontSize: 24, marginTop: "0", marginBottom: "16px", textAlign: "center" }} color="#FFFFFF" gutterBottom>
                    Approve
              </Typography>
              <Grid container>
              <Grid item xs={12} md={9}>
                <TextField inputRef={this.approveInput} label={"amount"} type="number" defaultValue={50} fullWidth={true}></TextField>
              </Grid>
              <Grid item xs={12} md={3}>
                <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
                  Approved Amount
                </Typography>
                <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.allowance, 4)} PSTN
                </Typography>
              </Grid>

                <Button fullWidth={true} sx={{my: 1}} disabled={window.connected === false} size="large" variant="contained" onClick={this.onClick_Approve}>{this.state.approveText}</Button>
                </Grid>
              </Grid>

              <Grid item xs={12} md={6}>
                  <Typography sx={{ fontWeight: "bold", fontSize: 24, marginTop: "0", marginBottom: "16px", textAlign: "center" }} color="#FFFFFF" gutterBottom>
                    Make a Deposit
                  </Typography>
                  <Typography variant="h2" sx={{ display: (this.state.referredBy.length > 1 && this.state.referredBy != "Will show after deposit") ? "none" : "block" }}>
                    <TextField
                      inputRef={this.sponsorInput}
                      value={this.state.sponsorAddress}
                      onChange={(event) => this.setState({ sponsorAddress: event.target.value })}
                      label={"Your Sponsor"}
                      type="text"
                      fullWidth={true}
                    ></TextField>
                  </Typography>

                  <Grid container>
                    <Grid item xs={12} md={9} sx={{ p: 0 }}>
                      <TextField inputRef={this.amountInput} inputProps={{ min: 10, max: 50000 }} defaultValue={10} label={"Amount in PSTN"} type="number" fullWidth={true}></TextField>
                    </Grid>
                    <Grid item xs={12} md={3} sx={{ p: 0 }}>
                      <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
                        Your Balance
                      </Typography>
                      <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
                        {formatEthFromWei(this.state.balance, 4)} PSTN
                      </Typography>
                    </Grid>
                  </Grid>
                  <br/>
                  <a target="_blank" href="https://pancake.kiemtienonline360.com/#/swap?outputCurrency=0xa7fb4C2ACe237f0FbAeD33C92Cb1c34f66FDAF99">Buy Piston on PancakeSwap</a>
                  <br/>
                  <br/>

                  <Button sx={{ my: 1 }} disabled={window.connected != true} size="small" variant="contained" onClick={this.onClick_Invest} fullWidth={true}>
                    deposit
                  </Button>
                </Grid>

                <Grid item xs={12} md={6}>
                  <Typography sx={{ fontWeight: "bold", fontSize: 24, marginTop: "32px", marginBottom: "16px", textAlign: "center" }} color="#FFFFFF" gutterBottom>
                    Pit Crew Deposit
                  </Typography>

                  <Grid container>
                    <Grid item xs={12} md={9} sx={{ p: 0 }}>
                      <TextField inputRef={this.stakeAmountInput} inputProps={{ min: 10, max: 50000 }} defaultValue={10} label={"Amount in PSTN"} type="number" fullWidth={true}></TextField>
                    </Grid>
                    <Grid item xs={12} md={3} sx={{ p: 0 }}>
                      <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
                        Your Balance
                      </Typography>
                      <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
                        {formatEthFromWei(this.state.balance, 4)} PSTN
                      </Typography>
                    </Grid>
                  </Grid>
                  <br/>

                  <Button sx={{ my: 1 }} disabled={window.connected != true} size="small" variant="contained" onClick={this.onClick_Stake} fullWidth={true}>
                    stake
                  </Button>

                  <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
                    Staked PSTN
                  </Typography>
                  <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
                    {formatEthFromWei(this.state.stakeBoostPSTN, 4)} PSTN
                  </Typography>
                  <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
                    Staked BUSD
                  </Typography>
                  <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
                    {formatEthFromWei(this.state.stakeBoostBUSD, 4)} BUSD
                  </Typography>

                  <Button sx={{ my: 1 }} disabled={window.connected != true} size="small" variant="contained" onClick={this.onClick_Unstake} fullWidth={true}>
                    unstake
                  </Button>


                  <Button sx={{ my: 1 }} disabled={window.connected != true} size="small" variant="contained" onClick={this.onClick_Eject} fullWidth={true}>
                    eject (within 1 hour after deposit)
                  </Button>
                </Grid>

          </Grid>
          <Grid item xs={12} md={6}>
              <div style={{color: "gray"}}>Deposits</div>
              <div>{formatEthFromWei(this.state.deposits, 10)} PSTN</div>
              <br></br>
              <div style={{color: "gray"}}>Available</div>
              <div>{formatEthFromWei(this.state.unwithdrawn, 10)} PSTN</div>
              <br/>
              <div style={{color: "gray"}}>Claimed</div>
              <div>{formatEthFromWei(this.state.withdrawn, 10)} PSTN</div>
              <br/>
              <div style={{color: "gray"}}>Referral Rewards</div>
              <div>{formatEthFromWei(this.state.ref_bonus, 10)} PSTN</div>
              <br/>
              <div style={{color: "gray"}}>Max Payout</div>
              <div>{formatEthFromWei(this.state.maxPayout, 10)} PSTN</div>
              <br/>
              <div style={{color: "gray"}}>Team</div>
              <div>{this.state.playerinfo}</div>
              <br/>
              <div style={{color: "gray"}}>Net Deposits</div>
              <div>{formatEthFromWei(this.state.netDeposits, 10)} PSTN</div>
              <br/>
              <div style={{color: "gray"}}>Real Deposits</div>
              <div>{formatEthFromWei(this.state.realDeposits, 10)} PSTN</div>

              <Button sx={{ my: 1 }} disabled={window.connected != true} size="small" variant="contained" onClick={this.onClick_Reinvest} fullWidth={true}>
                    roll (reinvest)
              </Button>
              <Button sx={{ my: 1 }} disabled={window.connected != true} size="small" variant="contained" onClick={this.onClick_Withdraw} fullWidth={true}>
                    withdraw
              </Button>
          </Grid>
        </Grid>
      </Paper>
    </Grid>

    <Grid item xs={12} md={3}>
      <AirdropPanel balance={this.state.balance}></AirdropPanel>
    </Grid>

    
    <Grid item xs={12} md={3}>
      <Paper className={"minHeight-small"} style={{ padding: "16px" }}>
        <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
          Airdrop Statistics:
        </Typography>
        <Typography sx={{ fontSize: 18 }} color="text.secondary" gutterBottom>
          Last Airdrop Sent:
        </Typography>
        <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
          {this.state.myLastAirdropSent}
        </Typography>
        <Typography sx={{ fontSize: 18 }} color="text.secondary" gutterBottom>
          Airdrops Sent:
        </Typography>
        <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
          {formatEthFromWei(this.state.myAirdrops, 8)} PSTN
        </Typography>
        
        <Typography sx={{ fontSize: 18 }} color="text.secondary" gutterBottom>
         Airdrops Received:
        </Typography>
        <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
          {formatEthFromWei(this.state.myAirdropsReceived, 8)} PSTN
        </Typography>

      </Paper>
    </Grid>


  </Grid>

</Box>
    );
  }
}

export default App;
