import React from 'react';
import { Button, Box, Grid } from "@mui/material";
import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";
import {ContractContext} from '../assets/abi/farm-contract';
import {ContractContext as TokenContractContext} from '../assets/abi/piston-contract';
import PowerIcon from '@mui/icons-material/Power';
import PowerOffIcon from '@mui/icons-material/PowerOff';
import { StringMappingType } from 'typescript';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Dialog from '@mui/material/Dialog';
import Avatar from '@mui/material/Avatar';
import ListItemAvatar from '@mui/material/ListItemAvatar';

import walletConnectLogo from "../assets/img/walletconnect.png";
import metamaskLogo from "../assets/img/metamask.png"
import trustwalletLogo from "../assets/img/trustwallet.png"



interface ConnectButtonProps {
  style?: React.CSSProperties;
  nickname?: string;
}

class ConnectButton extends React.Component<ConnectButtonProps> {
 
  state = {
    WalletConnectSelectionDlgOpen: false,
    connected: false,
    networkWarning: ""
  }
  baseState: any;
  nickname?: string;
  constructor(props: ConnectButtonProps) {
      super(props);
      this.baseState = this.state;

      this.nickname = props.nickname;
  }

  componentDidMount = () => { 
      //this.handleConnectWallet();
      //this.handleConnectWallet_WalletConnect();
      
      try{
        let existingConnectionJSON = localStorage.getItem("walletconnect");
        // walletconnect
        if(existingConnectionJSON){
          let existingConnection = false;
          try{
            let conn = JSON.parse(existingConnectionJSON);
            existingConnection = conn.connected;
          }catch{
            existingConnection = false;
          }
          if(existingConnection){
            this.initialConnect_WalletConnect(); // reuse created connection. no Modal will show
          }
        }else{
          if(window.ethereum){
            this.initialConnect(); // trust/metamask
          }
        }
      }catch{ 
  
      }
  }

  componentWillUnmount = () => { 

  }

  componentWillReceiveProps(props: ConnectButtonProps) { 

    this.nickname = props.nickname;
  }

  initialConnect_WalletConnect = async () => {

    if(window.chainId > 0 && window.chainIdExpected != window.chainId){
      this.setState({networkWarning: `network ${window.chainId} not supported.`});
      return;
    }else{
      this.setState({networkWarning: ``});
    }

    //  Create WalletConnect Provider
    window.walletConnectProvider = new WalletConnectProvider({
      infuraId: "042b9beb246dccdd1b2ced3804dfc2d9", // Required
      chainId: window.chainIdExpected, // 
      rpc: {
        97: "https://data-seed-prebsc-1-s1.binance.org:8545", // bsc testnet
        56: "https://bsc-dataseed.binance.org/" // bsc mainnet
      }/*,
      qrcodeModalOptions: {
        mobileLinks: [
          "encrypted ink"
        ],
        desktopLinks: [
          "encrypted ink"
        ]
      }*/
    });
    //  Enable session (triggers QR Code modal)
    try{

      await window.walletConnectProvider.enable();    

      window.web3 = new Web3(window.walletConnectProvider as any);

      const accounts = await window.web3.eth.getAccounts();
      if(accounts.length > 0) {
        window.connected = true;
        this.setState({connected: true});
        window.walletAddress = accounts[0];
        this.handleConnectWallet_WalletConnect();
      }
    }catch{
      console.log("error on walletconnect");
    }

  }

  initialConnect = () => {
    if (window.ethereum) {

      if(window.chainId > 0 && window.chainIdExpected != window.chainId){
        this.setState({networkWarning: `network ${window.chainId} not supported.`});
        return;
      }else{
        this.setState({networkWarning: ``});
      }

      window.ethereum
        .request({ method: "eth_requestAccounts" })
        .then((res: string[]) => {
          if (res.length >= 1) {
            window.connected = true;
            this.setState({connected: true});
            window.walletAddress = res[0];
            this.handleConnectWallet();
          }
        });
    } else if (
      window.confirm(
        "You have to use MetaMask or TrustWallet to connect. Press OK to continue to the MetaMask download page. https://metamask.io/download"
      )
    ) {
      let a = document.createElement("a");
      a.target = "_blank";
      a.href = "https://metamask.io/download";
      a.click();
    }
  }

  handleConnectWallet = () => {

    if(window.ethereum){
      window.web3 = new Web3(window.ethereum);

      window.chainId = window.ethereum.networkVersion;
      if(window.chainId > 0 && window.chainIdExpected != window.chainId){
        this.setState({networkWarning: `network ${window.chainId} not supported.`});
      }else{
        this.setState({networkWarning: ``});
      }

      window.ethereum.request({ method: "eth_accounts" }).then((res: any) => {
        if (res.length >= 1) {
          window.connected = true;
          this.setState({connected: true});
          window.walletAddress = res[0];
          this.updateRefLink();
          //console.log("eth_accounts", res);
          this.forceUpdate();

          window.farm_contract = (new window.web3.eth.Contract(window.contract_abi, window.contractAddress) as unknown) as ContractContext;
          window.token_contract = (new window.web3.eth.Contract(window.token_contract_abi, window.tokenContractAddress) as unknown) as TokenContractContext;
        }
      });
    }
    
  }

  handleConnectWallet_WalletConnect = async () => {

    if(window.walletConnectProvider){
      //window.web3 = new Web3(window.walletConnectProvider);
      window.chainId = window.walletConnectProvider.chainId;
      if(window.chainId > 0 && window.chainIdExpected != window.chainId){
        this.setState({networkWarning: `network ${window.chainId} not supported.`});
      }else{
        this.setState({networkWarning: ``});
      }

      const accounts = await window.web3.eth.getAccounts();
      if(accounts.length > 0) {
        window.connected = true;
        this.setState({connected: true});
        window.walletAddress = accounts[0];
        this.updateRefLink();
        this.forceUpdate();

        window.farm_contract = (new window.web3.eth.Contract(window.contract_abi, window.contractAddress) as unknown) as ContractContext;
        window.token_contract = (new window.web3.eth.Contract(window.token_contract_abi, window.tokenContractAddress) as unknown) as TokenContractContext;
      }
    }
    
  }

  handleDeactivateAccount = () => {
    window.connected = false;
    window.walletAddress = "";
    this.setState({networkWarning: ``});
    this.setState({connected: false});
    this.forceUpdate();
  }

  handleDeactivateAccount_WalletConnect = () => {
    window.walletConnectProvider.disconnect();
    window.connected = false;
    window.walletAddress = "";
    window.chainId = 0;
    localStorage.setItem("walletconnect", "");
    this.setState({networkWarning: ``});
    this.setState({connected: false});
    this.forceUpdate();
  }

  updateRefLink = () => {
    let refLinkWalledId = document.getElementById("walletAddressRefLink");
    if(refLinkWalledId != null){
      refLinkWalledId.innerText = window.walletAddress;
    }
  }

  handleWalletConnectDlgSelection = (selection: string) => {
    this.setState({
      WalletConnectSelectionDlgOpen: false
    });

    switch(selection){
      case "metamask":
      case "trustwallet":
        this.initialConnect();
      break;

      case "walletconnect":
        this.initialConnect_WalletConnect();
      break;
    }
  }

  handleWalletConnectDlgClose = () => {
    this.setState({
      WalletConnectSelectionDlgOpen: false
    });
  }

  onClick_connect = () => {

    this.setState({
      WalletConnectSelectionDlgOpen: true
    });

    //initialConnect_WalletConnect
  }

  onClick_disconnect = () => {
    try{
      this.handleDeactivateAccount_WalletConnect();
    }catch{

    }

    try{
      this.handleDeactivateAccount();
    }catch{

    }
  }

  render = () => {

    let button;
    if (this.state.connected) { 
      button = <Button fullWidth={false} variant={"contained"} color={"secondary"} sx={{my: 0, marginRight: 0 }} size="small" onClick={this.onClick_disconnect}><PowerOffIcon style={{height: "20px", marginRight: "8px"}}></PowerOffIcon>disconnect</Button>;    
    } else {
      button = <Button fullWidth={false} variant={"contained"} color={"primary"} sx={{my: 0, marginRight: 0 }} size="small" onClick={this.onClick_connect}><PowerIcon style={{height: "20px", marginRight: "8px"}}></PowerIcon>connect</Button>;  
    }

    let walletConnectPreSelectDlg =    <Dialog onClose={this.handleWalletConnectDlgClose} open={this.state.WalletConnectSelectionDlgOpen}>
      <List sx={{ p: "32px" }}>
        <ListItem autoFocus button onClick={() => this.handleWalletConnectDlgSelection('metamask')}>
          <ListItemAvatar>
              <img src={metamaskLogo} style={{width: "40px"}} />
            </ListItemAvatar>
          <ListItemText primaryTypographyProps={{fontSize: "24pt"}} primary="Metamask" />
        </ListItem>
        <ListItem autoFocus button onClick={() => this.handleWalletConnectDlgSelection('trustwallet')}>
            <ListItemAvatar>
              <img src={trustwalletLogo} style={{width: "40px"}} />
            </ListItemAvatar>
          <ListItemText primaryTypographyProps={{fontSize: "24pt"}} primary="Trust Wallet" />
        </ListItem>
        <ListItem autoFocus button onClick={() => this.handleWalletConnectDlgSelection('walletconnect')}>
            <ListItemAvatar>
              <img src={walletConnectLogo} style={{width: "40px"}} />
            </ListItemAvatar>
          <ListItemText primaryTypographyProps={{fontSize: "24pt"}} primary="WalletConnect" />
        </ListItem>
      </List>
    </Dialog>;

    return (
      <Box>
        {this.state.networkWarning}
        <Grid container>
          <Grid item xs={12} md={6}>
            {button}
          </Grid>

          <Grid item xs={12} md={6}>
            <div>
              {this.state.connected && window.walletAddress &&
                `${window.walletAddress.slice(0, 6)}...${window.walletAddress.slice(
                  window.walletAddress.length - 4,
                  window.walletAddress.length
                )}`}
                {this.nickname && ` (${this.nickname})`}
            </div>
          </Grid>

          
        </Grid>
        {walletConnectPreSelectDlg}
      </Box>
      );
    
  }
}

export default ConnectButton;