import Web3 from 'web3';

export function formatEthFromWei (value: string, pMaximumSignificantDigits: number = 4)  {
    value = Web3.utils.fromWei(value);
    return new Intl.NumberFormat('en-GB', {maximumSignificantDigits: pMaximumSignificantDigits}).format(parseFloat(value));
    //return parseFloat(value).toFixed(5).toLocaleString();
 };

 export function formatNumber (value: any, pMaximumSignificantDigits: number = 4)  {
    return new Intl.NumberFormat('en-GB', {maximumSignificantDigits: pMaximumSignificantDigits}).format(parseFloat(value));
 };
 
 export function formatWeiToUSD (value: string, digits: number = 4)  {
    return (parseFloat(Web3.utils.fromWei(value)) * window.bnbPrice).toFixed(digits);
 };

 export function formatBnbToUSD (value: number | string, digits: number = 4)  {
   return ((typeof(value) == "string"? parseFloat(value as string): value as number) * window.bnbPrice).toFixed(digits);
};